import DialogNormal from "~/components/utils/dialog/DialogNormal";
export default {
  name: 'myServerDialog',
  components: {DialogNormal},
  props: {
    top: {
      default: '10vh',
      type: String
    },
    widthStyle: {
      default: '550px',
      type: String
    },
    title: {
      default: '信息',
      type: String
    },
    subTitle: {
      default: '',
      type: String
    },
    visible: {
      default: false,
      type: Boolean
    },
    data: {
      type: Object
    }
  },
  computed: {
    selectModel(){
      this.value = this.selValue;
    }
  },
  data() {
    return {
      value: ''
    }
  },
  methods: {
    closeDialog(data){
      this.$emit("close", data);
    },
    handleBeforeClose(data){
      this.$emit("before-close", data);
    },
    handleClose(data){
      if (this.footer || this.showFooter == false){
        this.$emit("right-close", data);
      }else {
        this.handleCancel();
      }
    },
    handleOk(data){
      this.$emit("ok", data);
    },
    handleCancel(data){
      this.$emit("cancel", data);
    },
    detailClick(){
      this.$emit("detailClick", this.data);
    }
  }
}
